<!--   -->
<template>
  <div class="company_claims_warpper">
    <div class="search_container">
      <el-form :inline="true" ref="addFormInline" :model="params" class="demo-form-inline">
        <el-form-item label="" class="company_claims_setWidth" size="mini" label-width="55px">
          <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="" end-placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" size="mini" class="company_claims_setWidth" label-width="45px">
          <el-input v-model="params.tire_un" size="small" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" size="mini" class="company_claims_setWidth" label-width="55px">
          <v-GoodsList  :scopes='{row: params }' :updateArray="['goods_id']" :vxeParams='{company_id: portListParams.company_id}'  :showObj="{goods_id: '' , goods_name: ''}" />
        </el-form-item>
        <el-form-item size="mini" class="search-style search">
          <el-button @click="onSearch" size="mini" type="primary"></el-button>
          <el-button plain @click="onReset" size="mini"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="customTableWrapper">
      <custom-table max-height="500px" @selectChange="customTableSelection" :tableJson="quoteTableJson" :tableData="tableData" :table-loading="tableLoading"></custom-table>
      <div class="page_container">
        <div class="pageNumber"></div>
        <el-pagination class="erp-pagination" @size-change="sizeChangeCustomTable" @current-change="currentChangeCustomTable" :current-page.sync="goPage" :page-size="pageSize1" :page-sizes="per_page" :total="tablePage.total" layout="total,sizes, prev, pager, next,jumper" prevText="" nextText="">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import customTable from "common/components/public/popUp.vue"; // table
export default {
  props: {
    portListParams: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  data() {
    return {
      timeValue: [], // 
      tableLoading: false, // 
      tableData: [], // 
      tablePage: {
        per_page: 30, //
        page: 1, // 
        total: 0 // 
      }, // 
      // table 
      quoteTableJson: [
        {
          type: "selection",
          isChecked: true,
          width: 44
        },
        {
          prop: "brand_name",
          label: "",
          width: 110,
          slot: ""
        },
        {
          prop: "goods_name",
          label: "",
          width: 180,
          slot: ""
        },
        {
          prop: "goods_id",
          label: "",
          width: 80,
          slot: ""
        },
        {
          prop: "tire_un",
          label: "",
          width: 88,
          slot: ""
        },
        {
          prop: "symptom_name_str",
          label: "",
          width: 100,
          slot: ""
        },
        {
          prop: "raw_flowerdeep",
          label: "",
          width: 100,
          slot: ""
        },
        {
          prop: "surplus_flowerdeep",
          label: "",
          width: 100,
          slot: ""
        },
        {
          prop: "factory_flowerdeep",
          label: "",
          width: 100,
          slot: ""
        },
        {
          prop: "charge_flowerdeep",
          label: "",
          width: 100,
          slot: ""
        },
        {
          prop: "mm_wearprice",
          label: "",
          width: 100,
          slot: ""
        },
        {
          prop: "wear_price",
          label: "",
          width: 88,
          slot: ""
        },
        {
          prop: "intube_price",
          label: "",
          width: 88,
          slot: ""
        },
        {
          prop: "flap_price",
          label: "",
          width: 88,
          slot: ""
        },
        {
          prop: "inflap_price",
          label: "",
          width: 88,
          slot: ""
        },
        {
          prop: "purchase_price",
          label: "",
          width: 88,
          slot: ""
        },
        {
          prop: "factory_price",
          label: "",
          width: 88,
          slot: ""
        }
      ] // 
    };
  },

  components: {
    customTable
  },

  computed: {},

  mounted() {},

  methods: {
    //  
    portGetList(params) {
      let listParams = this.portListParams;
      listParams.per_page = this.tablePage.per_page;
      listParams.page = this.tablePage.page;
      if (params && params instanceof Object) {
        listParams = Object.assign({}, params, listParams);
      }
      this.tableLoading = true;
      this.$axMethod
        .getSupplierAppraisalList(listParams)
        .then(({ data }) => {
          this.tableLoading = false;
          if (data.code == 200) {
            this.tableData = data.data.data;
            this.tablePage.total = data.data.total;
          }
        })
        .catch(error => {
          this.tableLoading = false;
        });
    },
    // pageSize 
    sizeChangeCustomTable(pageSize) {
      this.tablePage.per_page = pageSize;
      this.portGetList(this.params);
    },
    // 
    currentChangeCustomTable(page) {
      this.tablePage.page = page;
      this.portGetList(this.params);
    },
    // 
    onReset() {
      this.$set(this, "params", {});
      this.timeValue = [];
      this.portGetList();
    },
    // 
    onSearch() {
      if (this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      this.portGetList(this.params);
    },
    // 
    customTableSelection(params) {
      let { selection } = params;
      this.$emit("quoteSelection", selection);
    }
  }
};
</script>
