          <el-table key="2" v-loading="qouteLoading" element-loading-text="" :highlight-current-row="true" tooltip-effect="light" ref="table" border :data="TableData.tableData" style="width: 100%" max-height="600" @selection-change="handleSelectionChange">
            <el-table-column :show-overflow-tooltip="true" type="selection" align="center" width="44">
