            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="factory_flowerdeep" label="">
              <template slot-scope="scope">
                <el-input type="number" v-model="scope.row.factory_flowerdeep" @input="limitInput(scope.row, 'factory_flowerdeep')" @change="handleChange(scope.row, [1], 'factory_flowerdeep')" size="small" placeholder=""></el-input>
              </template>
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="charge_flowerdeep" label="">
