              </template>
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="intube_price" label="">
              <template slot-scope="scope">
                <el-input type="number" v-model="scope.row.intube_price" @input="limitInput(scope.row, 'intube_price')" @change="handleChange(scope.row, [3], 'intube_price')" size="small" placeholder=""></el-input>
              </template>
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="flap_price" label="">
              <template slot-scope="scope">
                <el-input type="number" v-model="scope.row.flap_price" @input="limitInput(scope.row, 'flap_price')" @change="handleChange(scope.row, [3], 'flap_price')" size="small" placeholder=""></el-input>
              </template>
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="inflap_price" label="">
              <template slot-scope="scope">
                <el-input type="number" v-model="scope.row.inflap_price" @input="limitInput(scope.row, 'inflap_price')" @change="handleChange(scope.row, [4], 'inflap_price')" size="small" placeholder=""></el-input>
              </template>
