            <el-table-column :show-overflow-tooltip="true" property="purchase_price" label="">
              <template slot-scope="scope">
                <el-form-item :prop="`tableData.${scope.$index}.purchase_price`" :rules='[{required: true,message: "",trigger: "blur"}]' size="mini">
                  <el-input type="number" v-model="scope.row.purchase_price" @input="limitInput(scope.row, 'purchase_price')" @change="handleChange(scope.row, [4], 'purchase_price')" size="small" placeholder=""></el-input>
                </el-form-item>
              </template>
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="factory_price" label=" ">
              <template slot-scope="scope">
                <el-form-item :prop="`tableData.${scope.$index}.factory_price`" :rules='[{required: true,message: "",trigger: "blur"}]' size="mini">
                  <el-input type="number" v-model="scope.row.factory_price" @input="limitInput(scope.row, 'factory_price')" @change="handleChange(scope.row, [5], 'factory_price')" size="small" placeholder=""></el-input>
