    <el-dialog class="elDialog_warpper" :close-on-click-modal="false" title="" :visible.sync="dialogClaimTableVisible" width="975px" top="50px">
      <div v-if="dialogShow == 2">
        <addChooseProducts ref="quoteList" @quoteSelection="arr => {quoteSelectionList = arr}" :portListParams="{company_id: params.company_id, supplier_id: params.supplier_id}"></addChooseProducts>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogClaimTableVisible = false"> </el-button>
          <el-button type="primary" @click="selectionQuote"> </el-button>
