      <div v-if="dialogShow == 3">
        <p>
          <span style="display: inline-block;width: 50%;text-align: left;padding-bottom: 10px;">{{goods_name.goods_name}}</span>
          <span style="display: inline-block;width: 48%;text-align: right;padding-bottom: 10px;">{{goods_num}}</span>
        </p>
        <el-table :highlight-current-row="true" tooltip-effect="light" border :data="claimTable" max-height="600">
          <el-table-column :show-overflow-tooltip="true" property="tire_un" label="" width="150"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="symptom_name" label=""></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="charge_flowerdeep" label="" align="right"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="purchase_price" label="" align="right" width="150"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="wear_price" label="" align="right"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="inflap_price" label="" align="right" width="150"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="factory_price" label="" align="right" :formatter="formatterFactory">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="remark" label=""></el-table-column>
        </el-table>
